import React, { Fragment, useContext } from 'react';
import PropTypes from 'prop-types';
import BlogContext from '../../../context/blog/blogContext';
import { useHistory } from 'react-router-dom';

const BlogItem = ({ blog }) => {
	const blogContext = useContext(BlogContext);
	const { deleteBlog, setCurrent, clearCurrent } = blogContext;
	let history = useHistory();
	const { pk_df_blog_page_id, blog_headline, blog_subtitle } = blog;

	const onDelete = () => {
		deleteBlog(pk_df_blog_page_id);
		clearCurrent();
	};

	const onEdit = () => {
		setCurrent(blog);
		history.push('/createnewblog');
	};

	return (
		<Fragment>
			<div
				style={{ padding: '1rem', fontFamily: 'Open Sans, sans-serif' }}
				className='card bg-light'>
				<h3 style={{ fontWeight: 700 }}>{blog_headline}</h3>
				<h5>{blog_subtitle}</h5>

				<br></br>

				<p>
					<button className='btn btn-dark btn-sm' onClick={onEdit}>
						Edit
					</button>
					<button
						className='btn btn-danger btn-sm float-right'
						onClick={() => {
							if (window.confirm('Delete the blog')) onDelete();
						}}>
						Delete
					</button>
				</p>
			</div>
			<br></br>
		</Fragment>
	);
};

BlogItem.propType = {
	blog: PropTypes.object.isRequired,
};

export default BlogItem;
